import { StyledDatePickerButton } from '#PRIVATE_StyledDatePickerButton';
import { useTranslationBundle } from '#hooks/UNSAFE_useTranslationBundle';
import { useUser } from '#hooks/UNSAFE_useUser';
import { BundleType } from '#resources/nls/bundle';
import { getNumericFormattedYear } from '#utils/UNSAFE_calendarDateUtils';
import { ComponentProps, useCallback } from 'preact/compat';
import { useCellNavigation } from '../UNSAFE_DatePicker/useCellNavigation';
import { useFocusableCell } from '../UNSAFE_DatePicker/useFocusableCell';

export type UseYearCellProps = {
  /**
   * Specifies if this year is today's year.
   */
  isCurrent: boolean;

  /**
   * Specifies if this year should be focused first when one
   * tabs into the YearsGridView.
   */
  isFocusable: boolean;

  /**
   * Specifies if this year is currently focused.
   */
  isFocused: boolean;

  /**
   * Specifies if this year is currently selected.
   */
  isSelected: boolean;

  /**
   * The year represented by this YearCell.
   */
  year: number;

  /**
   * A callback that will be invoked when a selection action is performed on this YearCell.
   * @param payload The payload containing the year represented by this cell.
   */
  onAction: (payload: { year: number }) => void;

  /**
   * A callback that will be invoked when mouse down is performed on this YearCell.
   * @param payload The payload containing the year represented by this cell.
   */
  onYearFocused: (payload: { year: number }) => void;

  /**
   * A callback that will be invoked when a navigation action is performed on this YearCell.
   * @param payload The payload specifying the direction of the navigation performed and the year.
   */
  onNavigation: (payload: {
    direction: 'up' | 'next' | 'down' | 'previous' | 'previousDecade' | 'nextDecade';
    year: number;
  }) => void;
};

/**
 * A custom hook that adds functionalities to the YearCell component and computes properties for the
 * StyledDatePickerButton.
 */
export const useYearCell = ({
  isCurrent,
  isFocusable,
  isFocused,
  isSelected,
  year,
  onAction,
  onNavigation,
  onYearFocused
}: UseYearCellProps) => {
  const { locale } = useUser();
  const yearText = getNumericFormattedYear(locale, year, 1);
  // custom aria label for selected and current year
  const translations = useTranslationBundle<BundleType>('@oracle/oraclejet-preact');
  const ariaLabel = `${yearText}${isCurrent ? `, ${translations.datePicker_currentDate()}` : ''}${
    isSelected ? `, ${translations.datePicker_selected()}` : ''
  }`;

  const buttonProps = useFocusableCell({ isFocusable, isFocused });
  const buttonEventHandlers = useCellNavigation({
    navigationMap: {
      ArrowDown: 'down',
      ArrowLeft: { ltr: 'previous', rtl: 'next' },
      ArrowRight: { ltr: 'next', rtl: 'previous' },
      ArrowUp: 'up',
      PageDown: 'previousDecade',
      PageUp: 'nextDecade'
    },
    payload: { year },
    onAction,
    onNavigation
  });

  // JET-73067 (can not set value on first click) - when the currently selected cell is not
  // in the viewport, the first click on any other cell scrolls that selection into view
  // instead of selecting it. To fix this, we update the focusableYear onmousedown, before
  // focus is transferred to YearsGridView, so that it does not try to focus or scroll to
  // the previously selected cell.
  const onMouseDown = useCallback(() => {
    onYearFocused({ year });
  }, [onYearFocused, year]);

  return {
    buttonProps: {
      'aria-label': ariaLabel,
      isActivable: true,
      label: yearText,
      isHoverable: true,
      isSelected,
      isToday: isCurrent,
      variant: 'year' as const,
      onMouseDown,
      ...buttonProps,
      ...buttonEventHandlers
    } as ComponentProps<typeof StyledDatePickerButton>
  };
};
