import { StyledDatePickerButton } from '#PRIVATE_StyledDatePickerButton';
import { UseYearCellProps, useYearCell } from './useYearCell';

type PickedUseYearCellProps = Pick<
  UseYearCellProps,
  | 'isCurrent'
  | 'isFocusable'
  | 'isFocused'
  | 'isSelected'
  | 'year'
  | 'onAction'
  | 'onYearFocused'
  | 'onNavigation'
>;
type Props = PickedUseYearCellProps;

/**
 * This component renders the individual year cell in the YearsGridView.
 */
export const YearCell = (props: Props) => {
  const { buttonProps } = useYearCell(props);
  return <StyledDatePickerButton {...buttonProps} />;
};
