import { StyledDatePickerButton } from '#PRIVATE_StyledDatePickerButton';
import { UseMonthCellProps, useMonthCell } from './useMonthCell';

type PickedUseMonthCellProps = Pick<
  UseMonthCellProps,
  | 'isCurrent'
  | 'isFocusable'
  | 'isFocused'
  | 'isSelected'
  | 'month'
  | 'onAction'
  | 'onMonthFocused'
  | 'onNavigation'
>;
type Props = PickedUseMonthCellProps;

/**
 * This component renders the individual month cell in the MonthsGridView.
 */
export const MonthCell = (props: Props) => {
  const { buttonProps } = useMonthCell(props);
  return <StyledDatePickerButton {...buttonProps} />;
};
