import { StyledDatePickerButton } from '#PRIVATE_StyledDatePickerButton';
import { useTranslationBundle } from '#hooks/UNSAFE_useTranslationBundle';
import { useUser } from '#hooks/UNSAFE_useUser';
import { BundleType } from '#resources/nls/bundle';
import { getMonthName } from '#utils/PRIVATE_dayUtils';
import { IsoMonth } from '#utils/UNSAFE_calendarDateUtils';
import { ComponentProps, useCallback, useMemo } from 'preact/compat';
import { useCellNavigation } from '../UNSAFE_DatePicker/useCellNavigation';
import { useFocusableCell } from '../UNSAFE_DatePicker/useFocusableCell';

export type UseMonthCellProps = {
  /**
   * Specifies if this month is today's month.
   */
  isCurrent: boolean;

  /**
   * Specifies if this month should be focused first when
   * one tabs into the MonthsGridView.
   */
  isFocusable: boolean;

  /**
   * Specifies if this month is currently focused.
   */
  isFocused: boolean;

  /**
   * Specifies if this month is currently selected.
   */
  isSelected: boolean;

  /**
   * The month represented by this MonthCell.
   */
  month: IsoMonth;

  /**
   * A callback that will be invoked when a selection action is performed on this MonthCell.
   * @param payload The payload containing the month represented by this cell.
   */
  onAction: (payload: { month: IsoMonth }) => void;

  /**
   * A callback that will be invoked when mouse down is performed on this MonthCell.
   * @param payload The payload containing the month represented by this cell.
   */
  onMonthFocused: (payload: { month: IsoMonth }) => void;

  /**
   * A callback that will be invoked when a navigation action is performed on this MonthCell.
   * @param payload The payload specifying the direction of the navigation performed and the month.
   */
  onNavigation: (payload: {
    direction: 'up' | 'next' | 'down' | 'previous';
    month: IsoMonth;
  }) => void;
};

/**
 * A custom hook that adds functionalities to the MonthCell component and computes properties for the
 * StyledDatePickerButton.
 */
export const useMonthCell = ({
  isCurrent,
  isFocusable,
  isFocused,
  isSelected,
  month,
  onAction,
  onMonthFocused,
  onNavigation
}: UseMonthCellProps) => {
  const { locale } = useUser();
  const monthText = useMemo(
    () => ({
      short: getMonthName(locale, month, '*', 'short') as string,
      long: getMonthName(locale, month, '*', 'long') as string
    }),
    [locale, month]
  );

  // custom aria label for selected and current month
  const translations = useTranslationBundle<BundleType>('@oracle/oraclejet-preact');
  const ariaLabel = `${monthText.long}${
    isCurrent ? `, ${translations.datePicker_currentDate()}` : ''
  }${isSelected ? `, ${translations.datePicker_selected()}` : ''}`;

  const buttonProps = useFocusableCell({ isFocusable, isFocused });
  const buttonEventHandlers = useCellNavigation({
    navigationMap: {
      ArrowDown: 'down',
      ArrowLeft: { ltr: 'previous', rtl: 'next' },
      ArrowRight: { ltr: 'next', rtl: 'previous' },
      ArrowUp: 'up'
    },
    payload: { month },
    onAction,
    onNavigation
  });

  // JET-73067 (can not set value on first click) - when the currently selected cell is not
  // in the viewport, the first click on any other cell scrolls that selection into view
  // instead of selecting it. To fix this, we update the focusableMonth onmousedown, before
  // focus is transferred to MonthsGridView, so that it does not try to focus or scroll to
  // the previously selected cell.
  const onMouseDown = useCallback(() => {
    onMonthFocused({ month });
  }, [month, onMonthFocused]);

  return {
    buttonProps: {
      'aria-label': ariaLabel,
      isActivable: true,
      label: monthText.short,
      isHoverable: true,
      isSelected,
      isToday: isCurrent,
      variant: 'month' as const,
      onMouseDown,
      ...buttonProps,
      ...buttonEventHandlers
    } as ComponentProps<typeof StyledDatePickerButton>
  };
};
